.container {
  max-width: 1460px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media (max-width: 1024px) {
  .container {
    max-width: 1024px;
  }
}

@media (max-width: 767px) {
  .container {
    max-width: 767px;
  }
}

@media (max-width: 320px) {
  .container {
    max-width: 320px;
  }
}

